<template>
  <div class="page-home page">
    <section class="nav">
      <router-link :to="{ name: 'STARTED' }">
        
      </router-link>
      <router-link :to="{ name: 'HOME' }">
        
      </router-link>
      <router-link :to="{ name: 'HOME' }">
        
      </router-link>
      <router-link :to="{ name: 'TRACK_VIEW' }">
        
      </router-link>
      <router-link :to="{ name: 'BLOCK_SHOW' }">
        
      </router-link>
      <a href="https://github.com/l-hammer/v-track/issues"> issue</a>
    </section>

    <!----------------------- DEMO 1 ------------------------>
    <section class="demo">
      <el-alert
        center
        type="info"
        title=""
        :closable="false"
      >
      </el-alert>
      <div class="section-content">
        <Button v-track:18015.custom-event="handleCustomEvent"></Button>
      </div>
    </section>

    <section class="snippets">
      <CodeSnippet class="snippet" lang="html" :code="ButtonComponentSnippet" />
      <CodeSnippet
        class="snippet"
        lang="html"
        :code="trackCustomEventSnippet"
      />
    </section>

    <!----------------------- DEMO 2 ------------------------>
    <section class="demo">
      <el-alert
        center
        type="info"
        title=""
        :closable="false"
      >
      </el-alert>
      <div class="section-content">
        <Button
          v-track:18019.custom-event="handleCustomEventWithParam"
        ></Button>
      </div>
    </section>

    <section class="snippets">
      <CodeSnippet
        class="snippet"
        lang="html"
        :code="ButtonComponentWithParamSnippet"
      />
      <CodeSnippet
        class="snippet"
        lang="html"
        :code="trackCustomEventWithParamSnippet"
      />
    </section>

    <!----------------------- DEMO 3 ------------------------>
    <section class="demo">
      <el-alert
        center
        type="info"
        title="activeNameFeedbackactiveNamename"
        :closable="false"
      >
      </el-alert>
      <div class="section-content collapse-wrapper">
        <el-collapse
          accordion
          v-model="activeName"
          v-track:18020.change.delay="handleChange"
        >
          <el-collapse-item title=" Feedback" name="Feedback">
            <div>
              
            </div>
            <div></div>
          </el-collapse-item>
          <el-collapse-item title=" Efficiency" name="Efficiency">
            <div></div>
            <div>
              
            </div>
            <div>
              
            </div>
          </el-collapse-item>
          <el-collapse-item title=" Controllability" name="Controllability">
            <div>
              
            </div>
            <div>
              
            </div>
          </el-collapse-item>
        </el-collapse>
      </div>
    </section>

    <section class="snippets">
      <CodeSnippet
        class="snippet"
        lang="html"
        :code="trackCustomEventDelaySnippet"
      />
      <CodeSnippet
        class="snippet"
        lang="js"
        :code="jsTrackCustomEventDelaySnippet"
      />
    </section>

    <!----------------------- DEMO 4 ------------------------>
    <section class="demo">
      <el-alert
        center
        type="info"
        title="restnullfetchsuccessrestsuccess"
        :closable="false"
      >
      </el-alert
      ><el-alert
        center
        type="warning"
        title="DOM rest "
        :closable="false"
      >
      </el-alert>
      <div class="section-content">
        <Button v-track:18021.custom-event.async="{ fetchRest, rest }"></Button>
      </div>
    </section>

    <section class="snippets">
      <CodeSnippet
        class="snippet"
        lang="html"
        :code="trackCustomEventAsyncSnippet"
      />
      <CodeSnippet
        class="snippet"
        lang="js"
        :code="jsTrackCustomEventAsyncSnippet"
      />
    </section>
  </div>
</template>

<script>
import CodeSnippet from "../components/code-snippet";
import Button from "../components/button";

const ButtonComponentSnippet = `
<!-- button  -->
<div class="track-button" @click="$emit('custom-event')">
  click me
</div>
`;
const ButtonComponentWithParamSnippet = `
<!-- button  -->
<div class="track-button" @click="e => $emit('custom-event', { id: Date.now().toString(36) }, e)">
  click me
</div>
`;
const trackCustomEventSnippet = `
<Button v-track:18015.custom-event="handleCustomEvent"></Button>
`;
const trackCustomEventWithParamSnippet = `
<Button v-track:18019.custom-event="handleCustomEventWithParam"></Button>
`;
const trackCustomEventDelaySnippet = `
<el-collapse
  accordion
  v-model="activeName"
  v-track:18020.change.delay="handleChange"
>
  <el-collapse-item title="" name="Feedback">...</el-collapse-item>
  <el-collapse-item title="" name="Efficiency">...</el-collapse-item>
  <el-collapse-item title="" name="Controllability">...</el-collapse-item>
</el-collapse>
`;
const jsTrackCustomEventDelaySnippet = `
export default {
  data() {
    return {
      activeName: "Feedback",
    };
  },
  methods: {
    handleChange(val) {
      this.activeName = val;
    }
  }
}
`;
const trackCustomEventAsyncSnippet = `
<Button v-track:18021.custom-event.async="{ fetchRest, rest }"></Button>
`;
const jsTrackCustomEventAsyncSnippet = `
export default {
  data() {
    return {
      rest: null
    };
  },
  methods: {
    async fetchRest() {
      const response = await new Promise(resolve => {
        setTimeout(() => {
          resolve({ data: "success" });
        }, 300);
      });

      this.rest = response.data;
    }
  }
}
`;

export default {
  name: "CustomEvents",
  components: {
    Button,
    CodeSnippet
  },
  data() {
    return {
      ButtonComponentSnippet,
      ButtonComponentWithParamSnippet,
      trackCustomEventSnippet,
      trackCustomEventWithParamSnippet,
      trackCustomEventDelaySnippet,
      trackCustomEventAsyncSnippet,
      jsTrackCustomEventDelaySnippet,
      jsTrackCustomEventAsyncSnippet,
      item: {
        id: Math.random()
          .toString(36)
          .substr(2)
      },
      activeName: "Feedback",
      rest: null
    };
  },
  methods: {
    handleCustomEvent() {
      this.$message.success("");
    },
    handleCustomEventWithParam(item, { target }) {
      this.$message.success(
        `${JSON.stringify(item)}--${target}`
      );
    },
    handleChange(val) {
      this.activeName = val;
    },
    async fetchRest() {
      const response = await new Promise(resolve => {
        setTimeout(() => {
          resolve({ data: "success" });
        }, 300);
      });

      if (this.rest === response.data) {
        this.$message.success(
          ""
        );
      } else {
        this.rest = response.data;
        this.$message.success("");
      }
    }
  }
};
</script>
