import trackAction from "./action";

export default {
  /**
   * @name UVPV 
   * @desc UVPV
   * @param {Object} context 
   */
  UVPV() {
    trackAction("1");
  },
  /**
   * @name TONP 
   * @desc Time on Page
   * @param {Object} context 
   * @param {Timestamp} et 
   * @param {Timestamp} dt 
   */
  TONP(_, { et, dt }) {
    trackAction("2", {
      stt: `${(dt - et) / 1e3}s`
    });
  },
  /**
   * @desc 
   */
  18015({ $route: { name } }) {
    trackAction("18015", {
      source_page: name // 
    });
  },
  /**
   * @param {Object} context 
   * @param {Object} item 
   * @param {Object} event 
   */
  18016(
    {
      $route: { name }
    },
    { id },
    { target }
  ) {
    trackAction("18016", {
      id,
      source_page: name,
      target
    });
  },
  18017({ index, $route: { name } }) {
    trackAction("18017", {
      source_page: name,
      index
    });
  },
  18018({ rest, $route: { name } }) {
    trackAction("18018", {
      source_page: name,
      rest
    });
  },
  18019(
    {
      $route: { name }
    },
    { id },
    { target }
  ) {
    trackAction("18019", {
      id,
      source_page: name,
      target
    });
  },
  18020({ activeName, $route: { name } }) {
    trackAction("18020", {
      source_page: name,
      active_name: activeName
    });
  },
  18021({ rest, $route: { name } }) {
    trackAction("18021", {
      source_page: name,
      rest
    });
  },
  18022({ $route: { name } }) {
    trackAction("18022", {
      source_page: name,
      description: ""
    });
  },
  18023({ $route: { name } }) {
    trackAction("18023", {
      source_page: name
    });
  },
  18025({ rest1, $route: { name } }) {
    trackAction("18025", {
      source_page: name,
      rest: rest1
    });
  },
  18026({ rest2, $route: { name } }) {
    trackAction("18026", {
      source_page: name,
      rest: rest2
    });
  },
  18027({ $route: { name } }) {
    trackAction("18027", {
      source_page: name,
      description: ""
    });
  },
  18028({ $route: { name } }) {
    trackAction("18028", {
      source_page: name,
      description: ""
    });
  }
};
